$themeColor: #007aff !default;
$colors: (
  'white': #ffffff,
  'black': #000000,
) !default;


@font-face {
  font-family: 'swiper-icons';
  src: url('data:application/font-woff;charset=utf-8;base64, ')
    format('woff');
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #{$themeColor};
}
.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}
.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}
.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}
.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}
.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}
.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}
.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}
.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}
.swiper-slide-invisible-blank {
  visibility: hidden;
}
/* Auto Height */
.swiper-container-autoheight {
  &,
  .swiper-slide {
    height: auto;
  }

  .swiper-wrapper {
    align-items: flex-start;
    transition-property: transform, height;
  }
}

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px;
  .swiper-wrapper,
  .swiper-slide,
  .swiper-slide-shadow-left,
  .swiper-slide-shadow-right,
  .swiper-slide-shadow-top,
  .swiper-slide-shadow-bottom,
  .swiper-cube-shadow {
    transform-style: preserve-3d;
  }
  .swiper-slide-shadow-left,
  .swiper-slide-shadow-right,
  .swiper-slide-shadow-top,
  .swiper-slide-shadow-bottom {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 10;
  }
  .swiper-slide-shadow-left {
    background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-right {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-top {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-bottom {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
}

/* CSS Mode */
.swiper-container-css-mode {
  > .swiper-wrapper {
    overflow: auto;
    scrollbar-width: none; /* For Firefox */
    -ms-overflow-style: none; /* For Internet Explorer and Edge */
    &::-webkit-scrollbar {
      display: none;
    }
  }
  > .swiper-wrapper > .swiper-slide {
    scroll-snap-align: start start;
  }
}
.swiper-container-horizontal.swiper-container-css-mode {
  > .swiper-wrapper {
    scroll-snap-type: x mandatory;
  }
}
.swiper-container-vertical.swiper-container-css-mode {
  > .swiper-wrapper {
    scroll-snap-type: y mandatory;
  }
}


.swiper {
  width: 100%;
  max-width: 1170px;
  height: 583px;
  @include tablet {
    height: 615px;
  }
  @include mobile {
    height: 409px;
  }
}
.swiper h2 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 700;
  text-align: center;
  @include tablet {
    line-height: 40px;
  }
  @include mobile {
    font-size: 33px;
    margin: 0 0 20px;
  }
}
.swiper__button-container {
  display: flex;
  justify-content: space-between;
  margin: 0 0 15px;
  @include tablet {
    margin: 0 0 4px;
  }
  @include mobile {
    display: none;
  }
}
.swiper-button-prev-slide,
.swiper-button-next-slide {
  width: 29px;
  height: 9px;
  cursor: pointer;
  @include tablet {
    width: 36px;
    height: 15px;
  }
}
.swiper a {
  width: max-content;
  line-height: 20px;
  display: block;
  color: $text-color;
  border-bottom: 1px solid $border-color;
  margin: 0 auto 59px;
  cursor: pointer;
  @include tablet {
    line-height: 26px;
    margin: 0 auto 51px;
  }
  @include mobile {
    margin: 0 auto 40px;
  }
}
.swiper-wrapper {
  width: 100%;
}

.swiper-slide img {
  width: 100%;
  max-width: 270px;
  height: auto;
  max-height: 282px;
  display: block;
  margin: 0 auto;
  @include tablet {
    width: 324px;
    max-width: 324px;
    height: 340px;
    max-height: none;
  }
  @include mobile {
    width: 130px;
    height: 136px;
  }
}

.swiper-slide figcaption {
  font-weight: 500;
  text-align: center;
  margin: 24px 0 5px;
  @include tablet {
    margin: 22px 0 4px;
  }
  @include mobile {
    margin: 16px 0 5px;
  }
}

.swiper-slide p {
  font-size: 15px;
  font-weight: 700;
  text-align: center;
}

.swiper-pagination-fraction {
  font-size: 16px;
  line-height: 20.8px;
  font-weight: 500;
}

.swiper-pagination-fraction ul {
  display: flex;
  justify-content: center;
  gap: 15px;
  @include mobile {
    gap: 10px;
  }
}

.swiper-pagination {
  color:  black;
}

.swiper-pagination-bullet {
  width: 14px;
  height: 21px;
  font-family: inherit;
  font-size: 16px;
  line-height: 20.8px;
  font-weight: 500;
  color: #9B9997;
  opacity: 100%;
  border-radius: none;
  background: transparent;

}
.swiper-pagination-bullet-active {
  color: #1D1613;
}
